* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  background: #000;
  position: fixed;
  overflow: hidden;
}

#canvas {
  width: 100vw;
  height: 100vh;
}

.portraitAlert {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
}

.rotate_device_container {
  position: relative;
}

.rotate_device__p {
  margin-bottom: 40px;
  font-size: 18px;
  position: relative;
}

.rotate_device_icon {
  height: 175px;
  width: 175px;
}

.rotate_device_arrow {
  height: 175px;
  width: 175px;
  position: absolute;
  top: 0;
  left: 0;
}

.active {
  animation: 2s cubic-bezier(.455, .03, .515, .955) infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  50% {
    opacity: 1;
    transform: rotate(-90deg);
  }

  75% {
    opacity: 0;
    transform: rotate(-90deg);
  }

  76% {
    transform: rotate(0);
  }

  100% {
    opacity: 1;
    transform: rotate(0);
  }
}

.visible {
  display: flex;
}

.preloader {
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: opacity 1s;
  position: absolute;
}

.preloader__wrapper {
  width: 100%;
  height: 100%;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: xx-large;
  display: flex;
}

.preloader__progress {
  width: 40%;
  position: relative;
}

.preloader__progress__back {
  width: 100%;
  height: 3px;
  background-color: gray;
  border-color: gray;
  position: absolute;
}

.preloader__progress__front {
  height: 3px;
  width: 0%;
  background: #f60d54;
  border-color: #f60d54;
  position: absolute;
}

